import { ADDFILES, LISTFILES } from "../Actions/Types";

const initialState = {
    Files: [],
    File: {},
    Error: null
};

export const FileReducer = (state = initialState, action) => {
    switch (action.type) {
        case LISTFILES:
            return {
                ...state,
                Files: action.payload.files,
                Error: action.payload.Error
            };
        default:
            return state
    }
}